import React, { useState } from 'react';
import Axis from './axis';
import GraphBody from './graph_body';
import MeanLine from './mean_line';

export default class OrderScaleShape extends React.Component {
  static defaultProps = { pb: 10, pl: 10, pt: 10, pr: 10};

  constructor(props) {
    super(props);
    this.state = {
      dragging: false,
      selectedEl: false
    };
    this.onMouseDown = this.onMouseDown.bind(this)
    this.onMouseMove = this.onMouseMove.bind(this)
    this.onMouseUp = this.onMouseUp.bind(this)
    this.coordsToSvgCoords = this.coordsToSvgCoords.bind(this)
    this.svgCoordsToCoords = this.svgCoordsToCoords.bind(this)
  }

  onMouseDown(i) {
    return ((e) => {
      this.setState({dragging: i, selectedEl: e.target})
    }).bind(this)
  }

  onMouseMove(e) {
    if (this.state.selectedEl) {
      // mouse coords to svg coords
      const CTM = this.refs.svg1.getScreenCTM()
      const svgy = (e.clientY - CTM.f) / CTM.d
      this.props.setCoords(this.state.dragging, this.svgCoordsToCoords([0, svgy])[1])
    }
  }

  onMouseUp() {
    this.setState({selectedEl: null})
    this.setState({dragging: false})
  }

  // x0, y0 are bottom left coordinates
  coordsToSvgCoords(coords) {
    const width = this.props.width - this.props.pl - this.props.pr
    const height = this.props.height - this.props.pb - this.props.pt
    const x0 = this.props.pl
    const y0 = this.props.height - this.props.pb

    // -this.props.data[0][0] to align data to left
    const svgx = x0 + (-this.props.data[0][0] + coords[0]) * width / (this.props.resolution[0]-1)
    const svgy = y0 - coords[1] * height / this.props.resolution[1]
    return [svgx, svgy]
  }

  svgCoordsToCoords(svgCoords) {
    const [svgx, svgy] = svgCoords
    const width = this.props.width - this.props.pl - this.props.pr
    const height = this.props.height - this.props.pb - this.props.pt
    const x0 = this.props.pl
    const y0 = this.props.height - this.props.pb

    const x = (svgx - x0)/width * (this.props.resolution[0]-1) + this.props.data[0][0]
    const y = (y0 - svgy) / height * this.props.resolution[1]
    return [x, y]
  }

  render() {
    return (
      <svg
        ref="svg1"
        className={this.props.className}
        viewBox={`0 0 ${this.props.width} ${this.props.height}`}
        onMouseMove={this.onMouseMove}
        onMouseUp={this.onMouseUp}>
        {/*<Axis
          x={this.props.pl}
          y={this.props.height - this.props.pb}
          length={this.props.width - this.props.pl - this.props.pr}
          horizontal={true}
        />
        <Axis
          x={this.props.pl}
          y={this.props.pt}
          length={this.props.height - this.props.pb - this.props.pt}
          horizontal={false}
        />*/}
        <GraphBody
          x={this.props.pl}
          y={this.props.height - this.props.pb}
          width={this.props.width - this.props.pl - this.props.pr}
          height={this.props.height - this.props.pb - this.props.pt}
          resolution={this.props.resolution}
          data={this.props.data}
          coordsToSvgCoords={this.coordsToSvgCoords} />
        <Dots
          data={this.props.data}
          onMouseDown={this.onMouseDown}
          coordsToSvgCoords={this.coordsToSvgCoords} />
        <PercLabels
          data={this.props.data}
          coordsToSvgCoords={this.coordsToSvgCoords} />
        <PriceLabels
          data={this.props.data}
          coordsToSvgCoords={this.coordsToSvgCoords} />
        <MeanLine
          data={this.props.data}
          coordsToSvgCoords={this.coordsToSvgCoords} />
        {"<!-- End Points -->"}
        {/*
        <circle cx={this.props.pl} cy={this.props.height - this.props.pb} r="2.5" fill="white" className="stroke-current text-blue-500"/>
        <circle cx={this.props.width-this.props.pr} cy={this.props.pt} r="2.5" fill="white" className="stroke-current text-blue-500"/>
        */}
      </svg>
    )
  }
}

export const Dots = ({data, onMouseDown, coordsToSvgCoords}) => {
  return data.map((coord, i) => {
    const [cx, cy] = coordsToSvgCoords(coord)
    return <circle
      key={i}
      cx={cx}
      cy={cy}
      r={2.5}
      className="fill-current text-blue-500 cursor-ns-resize hover:text-blue-200"
      onMouseDown={onMouseDown(i)}
      />
  })
}

export const PriceLabels = ({data, coordsToSvgCoords}) => {
  return data.map((coord, i) => {
    const cx = coordsToSvgCoords(coord)[0]
    return <text
      key={i}
      x={cx}
      y={100}
      textAnchor="middle"
      style={{fontSize:".4rem"}}
      className="text-gray-500 pointer-events-none select-none">
      {Math.round(data[i][0]*10)/10}
    </text>
  })
}

export const PercLabels = ({data, coordsToSvgCoords}) => {
  return data.map((coord, i) => {
    const [cx, cy] = coordsToSvgCoords(coord)
    return <text
      key={i}
      x={cx}
      y={cy-8}
      textAnchor="middle"
      style={{fontSize:".4rem"}}
      className="text-gray-500 pointer-events-none select-none font-thin">
      {Math.round(data.normalized[i][1]*100)}<tspan className="text-gray-400" style={{fontSize:".4rem"}}>%</tspan>
    </text>
  })
}
