import React, {Fragment} from 'react';

export default class MeanLine extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      avgText: `x̄`
    }
    this.onMouseEnter = this.onMouseEnter.bind(this)
    this.onMouseLeave = this.onMouseLeave.bind(this)
  }

  onMouseEnter() {
    this.setState({avgText: Math.round(this.props.data.mean*100)/100})
  }

  onMouseLeave() {
    this.setState({avgText: `x̄`})
  }

  render() {
    let [x1, y1] = this.props.coordsToSvgCoords([this.props.data.mean, 0])
    let [x2, y2] = this.props.coordsToSvgCoords([this.props.data.mean, 100])

    // For partial fill up to the mean
    const [mean, start, end] = [this.props.data.mean, this.props.data[0][0], this.props.data[this.props.data.length-1][0]]
    let perc = (mean-start)/(end-start) * 100

    // translucent overlay for fun effects
    return(
      <Fragment>
        <line x1={x1} y1={y1} x2={x2} y2={y2} className="stroke-current text-gray-500 stroke-1" strokeDasharray=".5 .5"/>
        <text
          x={x1}
          y={8}
          textAnchor="middle"
          style={{fontSize:".5rem", fontFamily:"auto"}}
          className="text-gray-500 select-none"
          onMouseEnter={this.onMouseEnter}
          onMouseLeave={this.onMouseLeave}>
          {this.state.avgText}
        </text>
      </Fragment>
      )
  }
}
